._coral-Clock {
  display: inline-table;
  vertical-align: top;
  width: 200px;
}
._coral-Clock._coral-Clock--extended {
  width: 250px;
}
._coral-Clock._coral-Clock--extended ._coral-Dropdown-label {
  min-width: 0;
}
._coral-Clock-minute,
._coral-Clock-hour {
  display: table-cell;
  width: 100%;
  min-width: 0;
}
._coral-Clock-divider {
  display: table-cell;
  text-align: center;
  width: 16px;
  vertical-align: middle;
  padding-top: 21px;
}
._coral-Clock ._coral-Clock-period {
  display: table-cell;
  min-width: 73px;
  width: 73px;
  vertical-align: top;
  padding-left: 16px;
  padding-top: 21px;
}
.coral--large ._coral-Clock-minute,
.coral--large ._coral-Clock-hour {
  width: 100%;
}
